.wallet {
  display: flex;
  padding: 1rem 3rem;
  flex-direction: row-reverse;
  margin-left: auto;

  @media screen and (max-width: 650px) {
    padding: 1rem 0.75rem;
  }
}

#headerButton {
  max-width: 400px;

  @media screen and (max-width: 650px) {
    max-width: 400px;
  }
}

.center {
  position: relative;
  width: 100%;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
}

.form>*+* {
  display: flex;
  flex-direction: column;
  margin-top: 2rem;
}

.form button {
  display: inline-flex;
  flex-direction: row;
  align-items: center;
  gap: 1rem;
  color: rgb(var(--on-accent-rgb));
  background-color: rgb(var(--accent-rgb));
  border: none;
  border-radius: var(--border-radius);
  padding: 1rem;
  font-size: 14px;
  font-weight: 600;
}

.form button:hover {
  cursor: pointer;
  background-color: rgb(var(--darker-accent-rgb));
}

.form button span {
  white-space: nowrap;
}

.form button svg {
  width: 1rem;
  fill: currentColor;
}

.field {
  display: block;
}

.field span {
  font-weight: 600;
  font-size: 14px;
}

.field input {
  display: block;
  width: 100%;
  padding: 1rem;
  margin-top: 0.5rem;
  border: 1px solid rgb(var(--tertiary-background-rgb));
  border-radius: var(--border-radius);
  background: rgb(var(--secondary-background-rgb));
  color: rgb(var(--hint-rgb));
  font-size: 1rem;
  line-height: 1.5;
}

.loading {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 1rem;
}

.loading svg {
  width: 2rem;
  height: 2rem;
  color: rgb(var(--accent-rgb));
  animation: spin 2s linear infinite;
}

@keyframes spin {
  100% {
    transform: rotate(360deg);
  }
}

.success {
  cursor: pointer;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
  gap: 1rem;
  background: rgb(var(--secondary-background-rgb));
  border: 1px solid rgb(var(--tertiary-background-rgb));
  border-radius: var(--border-radius);
  padding: 1rem;
}

.success>div {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.success code {
  font-family: var(--font-mono);
  color: rgb(var(--secondary-foreground-rgb));
}

.success svg {
  width: 2rem;
  height: 2rem;
  color: rgb(var(--accent-rgb));
}

.success:hover {
  background-color: rgb(var(--hover-background-rgb));
}

.success:hover code {
  color: rgb(var(--accent-rgb));
}


@import url('https://fonts.googleapis.com/css2?family=Inter&display=swap');

:root {
  font-family: 'Inter', sans-serif;
  height: 100%;
  --max-width: 1100px;
  --border-radius: 12px;

  --accent-rgb: 3, 255, 189;
  --on-accent-rgb: 0, 0, 0;

  --foreground-rgb: 255, 255, 255;
  --secondary-foreground-rgb: 143, 143, 143;

  --background-rgb: 18, 18, 18;
  --secondary-background-rgb: 27, 27, 27;
  --tertiary-background-rgb: 42, 42, 42;
}

html {
  height: 100%;


}


* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html,
body {

  max-width: 100vw;
  max-height: 100vh;
  overflow-x: hidden;

}



a {
  color: inherit;
  text-decoration: none;
}


button.wallet-adapter-button-trigger {
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  color: rgb(var(--foreground-rgb)) !important;
  border: 1px solid rgb(var(--tertiary-background-rgb)) !important;
  border-radius: var(--border-radius) !important;
  background-color: #683EB5 !important;
  width: 100% !important;
  border-radius: 8px !important;
}

button.wallet-adapter-button-trigger:hover {
  background-color: rgb(var(--hover-background-rgb)) !important;
}

.wallet-adapter-dropdown {
  display: flex !important;
  width: 100% !important;
}


.timer-details {
  display: flex;
  align-items: center;
  justify-content: center;
  background: #1b174f;
  padding: 4px;
  border-radius: 4px;
  margin: 0px 2px;
  max-width: 24px;
}

.chakra-button {
  white-space: unset !important;
  min-width: fit-content !important;
}